@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.card-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
}

.card-link {
    color: black;
    text-decoration: none;
}

.card {
    border-radius: 25px;
    width: 210px;
    margin-top: 20px;
    border-color: rgb(7, 7, 7);
    text-align: center;
    background-color: rgb(64, 179, 162);
    box-shadow: 4px 4px 5px 0px rgba(50, 50, 50, 0.75);
}

.card:hover {
    transform: scale(1.05)
}

.card-title {
    margin-bottom: 5px;
    font-size: 25px;
}

.card-subtitle {
    margin: 0 0 10px 0;
    color: black;
}

.card-image {
    width: 200px;
    height: 200px;
    border-radius: 25px;
    border: solid;
    border-width: 1px 0 0 0;
    -webkit-filter: saturate(80%);
            filter: saturate(80%);
}

@media only screen and (min-width: 500px) {
    .card-container {
        flex-direction: row;
        flex-wrap: wrap;
        margin-top: 50px;
    }

}

@media only screen and (min-width: 1000px) {
    .card {
        width: 20vw;
        height: 400px;
        position: relative;
    }

    .card:hover {
        transform: scale(1);
    }

    .card-info {
        position: absolute;
        display: flex;
        justify-content: center;
        width: 100%;
        height: 100%;
        z-index: 999;
        transition: 0.3s
    }

    .card-info:hover {
        opacity: 0%;
    }

      
    .card-circle {
        background: white;
        display: flex;
        position:absolute;
        flex-direction: column;
        width: 170px;
        height: 170px;
        border-radius: 50%;
        box-shadow: 0 0 5px rgba(50, 50, 50, 0.75);
        top: 28%;
    }

    .card-image {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    .extra-padding {
        padding-top: 10px;
    }
}
.header-container {
    display: flex;
    flex-direction: column;
}

.logo {
    width: 200px;
    border-radius: 25px;
    text-align: center;
    height: 100px;
    background-color: rgb(64, 179, 162);
}

.logo-text {
    font-size: 30px;
    font-weight: bold;
    padding: 5px;
}

.nav-span-container {
    display: flex;
    justify-content: space-between;
    border-bottom: solid;
    border-width: 1px;
    border-color: rgb(255, 125, 95)
}

.header-link {
    color: black;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px
}

@media only screen and (min-width: 500px) {
    .header-container {
        flex-direction: row;
    }

    .logo {
        margin: 0;
    }

    .nav-span-container {
        border: none;
        flex-direction: column;
        justify-content: left;
        padding-top: 0px;
        align-content: flex-start; 
    }

    .nav-span {
        padding: 0 0 10px 50px;
    }
}
.project-container {
    display: flex;
    flex-direction: column;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3%;
}

.project-description {
    margin-bottom: 0px;
}

.project-title {
    text-align: center;
    margin-top: 0;
}

.project-image {
    max-height: 500px;
    margin-top: 30px;
}

#pokedex-image {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
}

.project-link {
    font-weight: bold;
    color: black;
    text-decoration: none;
    max-width: 200px;
}

